/* eslint-disable no-console */
console.log( 'view.js use this file for JavaScript code that only run in frontend on posts/page' );
/* eslint-enable no-console */


import domReady from '@wordpress/dom-ready';

domReady ( function() {
  // do something after dom loads 
  const polarImages = document.querySelectorAll('.polaroid img');

  polarImages.forEach(img => {
    const loader = document.createElement('div');

    loader.classList.add('loader');

    img.before(loader);

    img.onload = function () {
      loader.remove();
      img.classList.add('loaded');
    }


  })




} )